export const fnGetLatLng = () => {//
    return new Promise((resolve, reject) => {
        navigator.geolocation.getCurrentPosition(
            (location) => {
                resolve({
                    lat: location.coords.latitude,
                    lng: location.coords.longitude,
                })
            }//success
            ,
            (error) => {
                if (error.code === 1) alert('   ')
                else alert('   ')

                throw new Error(' ')//   
                // reject(new Error(' '))
            }//failed
        )
    })
}//fnGetLatLng

