import React, { useContext, useEffect, useState } from 'react';
import { AppContext } from '../../App';
import { fnGetWeatherData, fnSetWeatherInfo } from '../../js/weather';
import CompSvg from './CompSvg';
import { codeEnArr, codeKrArr } from '../../js/conditionCode';


const CompCurrentOutput = () => {

  const {
    _weatherData,
    _address, _setAddress, _addressKo, _addressEn,
    fnAppInit,

  } = useContext(AppContext)

  const {
    temp, snow,
    icon, bg, desc, windDeg, windSpeed, feelsLike,
    humidity, rain, uvi, uviDesc,
    sunrise, sunset, date, day, time, apm
  } = fnSetWeatherInfo(_weatherData.current, _weatherData.timezone)


  //console.log(_weatherData);//    :     _weatherData    

  // useEffect(()=>{ //      ..   
  // },[])


  const fnResetHandler = () => {
    let result = window.confirm('   ?')
    if (result) fnAppInit()
  }

  return (
    <div className='current-output' style={{ backgroundImage: `url(${process.env.PUBLIC_URL}/img/weather/${bg}.jpg)` }}>{/*  dksdp dlTsmsrj ckwdmfEo */}

      <button onClick={fnResetHandler} className='reload'>
        <i className="fa-solid fa-arrows-rotate"></i>
      </button>

      <address>{/*                       .     */}
        <span>[ {_addressKo} {_addressEn} ]</span>
        <span>[ {_addressKo} {_addressEn} ]</span>
        <span></span>
      </address>

      <div className="circle-container">
        <div className="sunset">
          <CompSvg currentData={_weatherData.current} />
          <time className='riseTime'>
            {sunrise}
          </time>
          <time className='setTime'>
            {sunset}
          </time>

        </div>{/* sunset */}
        <div className="circle-container-inner">
          <p className='temp'>
            <em>{temp}<sup>&#176;c</sup></em>
            <img src={`${process.env.PUBLIC_URL}/img/icons/${icon}.gif`} alt="" />
            <span>
              <i className="fa-solid fa-user"></i>
              {/* <i>Feel</i> */}
              <b>{feelsLike}<sup>&#176;c</sup></b>
           
            </span>
          </p>
          <p className="desc">"{desc}"</p>

          <p className="etc">
            <span className="wind">
              <i style={{ transform: `rotate(${windDeg - 45}deg)` }} className="fa-solid fa-location-arrow"></i>
              <i className="fa-solid fa-wind"></i>
              <b>{windSpeed}</b>
              <small>m/s</small>

            </span>
            <span className="humidity">
              <i className="fa-solid fa-droplet"></i>
              <b>{humidity}</b>
              <small>%</small>

            </span>
            <span className="uvi">
              <i className="fa-solid fa-sun"></i>
              <b>{uvi}</b>
              <small>{uviDesc}</small>

            </span>
            {
              (rain!==0) &&
                <span className="rain">
                  <i className="fa-solid fa-umbrella"></i>
                  <b>{rain}</b>
                  <small>mm/h</small>
                </span>
                
            }
            {
              (snow!==0)&&
              <span className="snow">
                <i className="fa-solid fa-snowflake"></i>
                <b>{snow}</b>
                <small>mm/h</small>
              </span>
               
            }{/*      ,  ~    0  */}
          </p>

          <p className='time-date'>
            <time className="time">
              <small>{apm}</small>
              {time}
            </time>
            <br />
            <time className="date">{date} ({day}) </time>

          </p>
        </div>

        <br />
        <br />
      </div>
    </div >
  );
};

export default CompCurrentOutput;

